.App {

  background-color: rgba(236, 233, 166, 1);
  color: purple;

}
a{
  text-decoration: none;
  color: purple;
}

.main{
  display: flex;
  margin-top: 2.5rem;
  gap: 2rem;
}

.left
{
  flex-grow: 2;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 2rem;

}

.right
{
  flex-grow: 1;
  padding: 0.5rem;
  border: 2px solid purple;
  border-radius: 0.5rem;
  box-shadow: 1px 1px 7px 2px purple;
  text-align: center;
}

#toolbar{
  display: flex;
  justify-content: space-between;
}

.blink{
  animation: blink 1.5s  infinite;
}

@keyframes blink{
  0%{
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.queryBar{
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
}

section{
  overflow-y: scroll;
  scroll-behavior: smooth;    /* <----- THE SECRET ---- */
}
